import { useState } from "react"
import { Button, Modal } from "react-bootstrap"
import { veryOtpaddbene, resendOtpsCustomer } from "../../../../../api/login/Login"
import { toastErrorMessage } from "../../../../compeleteRegister/ToastShare"


function Otpaddbeneficiary(props) {
    const [initialValue, setInitialValue] = useState({
        customer_mobile: '',
        user_id: '',
        otp: ''
    })

    console.log("propreferid",props.containreferid )

    const handleChange = (e) => {
        const clone = { ...initialValue }
        const value = e.target.value
        const name = e.target.name
        clone[name] = value
        setInitialValue(clone)
    }
    const resetForm = () => {
        setInitialValue({
            customer_mobile: '',
            user_id: '',
            otp: ''
        })
    }

    const otpSubmit = async () => {
        const clone = { ...initialValue, customer_mobile: props.initialCustomer.mobile,
            api_id:props.initialCustomer.api_id, user_id: window.localStorage.getItem('userIdToken'),recipient_id:props.containreferid }
        try {
            const res = await veryOtpaddbene(clone)
            if ( res?.data?.statusCode == 200) {
                props.toastSuccessMessage(res?.data?.message || res?.message )
                resetForm() 
                props.onHide()
                props.updateRecipetList();
                // if (props.initialCustomer.api_id === "66bca8dd5727c7563ad6e317") {
                //     props.setDmtKyc(true);
                // }
            }
            else{
               toastErrorMessage(res?.data?.message || res?.message ||"Something went Wrong")
            }
        } catch (error) {

        }
    }

    const ResentotpSubmit = async () => {
        const clone = { customer_mobile: props.customerNumber, user_id: window.localStorage.getItem('userIdToken') }
        try {
            const res = await resendOtpsCustomer(clone)
            if (res?.data?.statusCode == 200) {
                props.toastSuccessMessage(res?.data?.message)
                
            }
        } catch (error) {

        }
    }

    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                className="otp-customer"
            >
                <Modal.Header closeButton>
                    {/* <Modal.Title id="contained-modal-title-vcenter">
                        Modal heading
                    </Modal.Title> */}
                </Modal.Header>
                <Modal.Body>
                    <div className="tab-pane fade show in" id="Mobile" role="tabpanel" aria-labelledby="MobileTab">
                        <div className="row">
                            <div className="col-md-12 col-sm-12">
                                <h2 className="SecTitle-2">OTP Verification</h2>
                                <div className=" form-group">
                                    <div className="col-lg-12">
                                        <div className="form-group text-align-center">
                                            <div className="fl-wrap fl-wrap-input text-align-center">
                                                <label htmlFor="Name" className="fl-label ">OTP</label>
                                                <input type="text" className="form-control fl-input" id="Name" name="otp" value={initialValue.otp} placeholder="Enter OTP" onChange={handleChange} />
                                            </div>
                                        </div>
                                    </div>
                                    <div className="FormButtons text-align-center">
                                        <button type="button" className="btn btn-success me-2"   disabled={initialValue.otp.length !== 6}  onClick={otpSubmit}>OTP Submit</button>
                                        {/* <button type="button" className="btn btn-success" onClick={ResentotpSubmit}>OTP Resend</button> */}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </Modal.Body>
            </Modal>
        </>
    )
}
export default Otpaddbeneficiary