import React, { useEffect, useState } from "react";
import { Button, Modal } from "react-bootstrap";
import { FaRupeeSign } from "react-icons/fa";
import { sendMoneyTrans, verificationBank, sendbank3MoneyTransdmt } from "../../../../api/login/Login";
import TifinPopup from "./tifinPopup/TifinPopUp";
import { useNavigate } from 'react-router-dom';

function UserTransferWithOTP(props) {
    const [modalShow, setModalShow] = useState(false);
    const [recipientData, setRecipientData] = useState(null);
    const [isVerified, setIsVerified] = useState(false);
    const [isLoading, setIsLoading] = useState(false);
    const [storetid, setStoreTid] = useState(null);
    const [amountError, setAmountError] = useState('');
    const [channelKey, setChannelKey] = useState(1);
    const [showOtpInput, setShowOtpInput] = useState(false);
    const [otp, setOtp] = useState('');
    const [bank3Value, setBank3Value] = useState('');
    const [isGeneratingOtp, setIsGeneratingOtp] = useState(false);
    const [formDisabled, setFormDisabled] = useState(false);
    const otpLength = props.initialCustomer?.api_id === "66bca8ca5727c7563ad6e316" ? 4 : 6;
    const [tpinModalClosed, setTpinModalClosed] = useState(false);
    const [initialMoney, setInitialMoney] = useState({
        user_id: '',
        recipient_id: '',
        amount: '',
        customer_mobile: '',
        channel: 'NEFT',
        latlong: '',
        beneficiary_name: '',
        bank_name: '',
        ifsc_code: '',
        account_number: '',
        customer_name: ''
    });

    const navigate = useNavigate();

    // Get the maximum amount based on API ID
    const getMaxAmount = () => {
        if (props.initialCustomer?.api_id === "670e593e324d380f3be4fdb3") {
            return 25000;
        }
        return props.initialCustomer?.api_id ? 5000 : props.maxamount;
    };

    const resetInitialMoney = () => {
        setInitialMoney({
            user_id: '',
            recipient_id: '',
            amount: '',
            customer_mobile: '',
            channel: 'NEFT',
            latlong: '',
            beneficiary_name: '',
            bank_name: '',
            ifsc_code: '',
            account_number: '',
            customer_name: ''
        });
        setAmountError('');
        setOtp('');
        setBank3Value('');
        setShowOtpInput(false);
        setFormDisabled(false);
        setTpinModalClosed(false);
    };
    const handleTpinModalClose = () => {
        setTpinModalClosed(true);
        setFormDisabled(false);
        setShowOtpInput(false);
        setModalShow(false);
    };

    useEffect(() => {
        setRecipientData(props.sendMoneyData);
        setIsVerified(props.sendMoneyData?.isVerified || false);
    }, [props.sendMoneyData]);

    const handleAmount = (e) => {
        if (formDisabled) return;
        
        const value = e.target.value;
        const name = e.target.name;

        if (name === 'amount') {
            const amount = parseFloat(value);
            const maxAmount = getMaxAmount();
            
            if (isNaN(amount) || amount < 100) {
                setAmountError('Minimum amount is 100');
            } else if (amount > maxAmount) {
                setAmountError(`Maximum amount is ${maxAmount}`);
            } else {
                setAmountError('');
            }
        }

        setInitialMoney(prev => ({ ...prev, [name]: value }));
    };

    const handleOtpChange = (e) => {
        const value = e.target.value.replace(/\D/g, '').slice(0, otpLength);
        setOtp(value);
    };

    const handleGenerateOtp = async () => {
        setIsGeneratingOtp(true);
        try {
            const payload = {
                ...initialMoney,
                recipient_id: recipientData.recipient_id,
                customer_mobile: props.customerNumber,
                channel: channelKey,
                beneficiary_name: recipientData.bank_account_name || recipientData.recipient_name,
                bank_name: recipientData.bank,
                ifsc_code: recipientData.ifsc,
                account_number: recipientData.account,
                customer_name: props.customerinformation,
                bank_id: recipientData.bankId,
                api_id: props.initialCustomer.api_id,
                user_id: window.localStorage.getItem('userIdToken'),
                latlong: `${position.latitude},${position.longitude}`
            };

            const response = await sendbank3MoneyTransdmt(payload);
            
            if (response?.data?.statusCode === 200) {
                setBank3Value(response.data.data.value);
                setShowOtpInput(true);
                setFormDisabled(true);
                props.toastSuccessMessage("OTP sent successfully");
            } else {
                props.toastSuccessMessageError(response?.data?.message || "Failed to generate OTP");
            }
        } catch (error) {
            console.error("Error generating OTP:", error);
            props.toastSuccessMessageError("Failed to generate OTP");
        } finally {
            setIsGeneratingOtp(false);
        }
    };

    const handleChangeChanel = (str) => {
        if (formDisabled) return;
        const key = str === 'IMPS' ? 2 : 1;
        setChannelKey(key);
        setInitialMoney(prev => ({ ...prev, channel: str }));
    };

    const [position, setPosition] = useState({ latitude: null, longitude: null });

    useEffect(() => {
        if ("geolocation" in navigator) {
            navigator.geolocation.getCurrentPosition(function (position) {
                setPosition({
                    latitude: position.coords.latitude,
                    longitude: position.coords.longitude,
                });
            });
        } else {
            alert('Geolocation is not available in your browser.')
        }
    }, []);
    const tifinPopupOpen = () => {
        props.onHide();
        setModalShow(true);
    }

    const handleEnterPress = (e) => {
        if (e.key === 'Enter') {
            e.preventDefault();
            props.onHide();
            setModalShow(true);
        }
    };

    const sendMoney = async (tipin) => {
        setIsLoading(true);
        const payload = {
            ...initialMoney,
            tpin: tipin,
            recipient_id: recipientData.recipient_id,
            customer_mobile: props.customerNumber,
            channel: channelKey,
            beneficiary_name: recipientData.bank_account_name || recipientData.recipient_name,
            bank_name: recipientData.bank,
            ifsc_code: recipientData.ifsc,
            account_number: recipientData.account,
            customer_name: props.customerinformation,
            bank_id: recipientData.bankId,
            api_id: props.initialCustomer.api_id,
            user_id: window.localStorage.getItem('userIdToken'),
            latlong: `${position.latitude},${position.longitude}`,
            otp: otp,
            value: bank3Value
        };

        try {
            const res = await sendMoneyTrans(payload);
            if (res?.data?.statusCode === '200') {
                props.toastSuccessMessage(res?.data?.message);
                const tid = res?.data?.data?.[0]?.batchId;
                setStoreTid(tid);
                setModalShow(false);
                if (props.walletShowHeader) props.walletShowHeader();
                resetInitialMoney();
                setTimeout(() => {
                    navigate(`/details/${tid}`);
                }, 300);
            }
            else if (res?.data?.statusCode == 403) {
                props.toastSuccessMessageError(res?.data?.message);
            }
            
             else if (res?.data?.statusCode == 405 ) {
        
                props.onHide();
                resetInitialMoney();
                setModalShow(false)
                props.toastSuccessMessageError(res?.data?.message || "Invalid OTP");
            } 
          
            
            else if (res?.data?.error === true &&  res?.data?.statusCode !==(405 ||  403)) {
                props.onHide();
                resetInitialMoney();
                setModalShow(false)
                props.toastSuccessMessageError(res?.data?.message);
            }
        } catch (error) {
            console.error("Error sending money:", error);
            props.toastSuccessMessageError("An error occurred while sending money.");
        } finally {
            setIsLoading(false);
        }
    };

    const VerifiedBank = async () => {
        setIsLoading(true);
        const clone = {
            name: recipientData.recipient_name,
            ifsc: recipientData.ifsc,
            bankAccount: recipientData.account,
            phone: recipientData.recipient_mobile,
            recipient_id: recipientData.recipient_id,
            bank_id: recipientData.bankId,
            api_id: props.initialCustomer.api_id,
            user_id: window.localStorage.getItem('userIdToken')
        };

        try {
            const res = await verificationBank(clone);
            if (res?.data?.statusCode === '200') {
                props.toastSuccessMessage(res?.data?.message);
                setRecipientData(prevData => ({
                    ...prevData,
                    bank_account_name: res.data.data.bank_account_name
                }));
                setIsVerified(true);
                props.updateRecipetList();
            } else {
                props.toastSuccessMessageError(res?.data?.message || "Something Went Wrong");
                setIsVerified(false);
            }
        } catch (error) {
            console.error("Error verifying bank:", error);
            props.toastSuccessMessageError("An error occurred while verifying the bank account.");
            setIsVerified(false);
        } finally {
            setIsLoading(false);
        }
    }

    useEffect(() => {
        return () => {
            resetInitialMoney();
        };
    }, []);

    return (
        <>
            <Modal
                {...props}
                size="lg"
                aria-labelledby="contained-modal-title-vcenter"
                centered
                className="modal-dialog-modal"
                onHide={() => {
                    resetInitialMoney();
                    props.onHide();
                }}
            >
                <Modal.Header>
                    <Modal.Title id="contained-modal-title-vcenter">
                        <div className="head-modal text-align-center">
                            <h2>Send Money</h2>
                            <p>Domestic Money Transfer</p>
                        </div>
                    </Modal.Title>
                </Modal.Header>
                <Modal.Body>
                <div className="send-money-deatils">
                        <div className="send-money-name">
                            <h2>Name : </h2>
                            <p style={{
  whiteSpace: 'nowrap',
  overflow: 'hidden',
  textOverflow: 'ellipsis',
  maxWidth: '100%',
  display: 'inline-block',
  cursor: 'pointer'
}}
>{isLoading ? "Verifying..." : recipientData?.bank_account_name || recipientData?.recipient_name}</p>
                        </div>
                        {recipientData?.recipient_mobile && (
                            <div className="send-money-Mobile">
                                <h2>Mobile : </h2>
                                <p>{recipientData.recipient_mobile}</p>
                            </div>
                        )}
                        <div className="send-money-accNum">
                            <h2>IFSC Code : </h2>
                            <p>{recipientData?.ifsc}</p>
                        </div>
                        <div className="send-money-accNum">
                            <h2>Account Number : </h2>
                            <p>{recipientData?.account}</p>
                        </div>
                        <div className="send-money-bnkName">
                            <h2>Bank Name : </h2>
                            <p>{recipientData?.bank}</p>
                        </div>
                    </div>
                    
                    <div className="row">
                        <div className="col-lg-12">
                            <div className="send-moneyForm mt-1">
                                <form action="">
                                    <div className="input-group mb-3">
                                        <span className="input-group-text" id="basic-addon1"><FaRupeeSign /></span>
                                        <input 
                                            type="number" 
                                            className={`form-control ${amountError ? 'is-invalid' : ''}`}
                                            placeholder={`Enter Amount (Min: 100, Max: ${getMaxAmount()})`} 
                                            name="amount" 
                                            value={initialMoney.amount} 
                                            onChange={handleAmount}
                                            disabled={formDisabled}
                                            onKeyPress={handleEnterPress} 
                                        />
                                    </div>
                                    {amountError && <div className="invalid-feedback d-block mb-1">{amountError}</div>}
                                    
                                    {!showOtpInput ? (
                                        <button 
                                            type="button"
                                            className={`btn ${!initialMoney?.amount || amountError ? 'commonbotton_disable' : 'btn-success'} btn-cnf`} 
                                            disabled={!initialMoney?.amount || amountError || isGeneratingOtp} 
                                            onClick={handleGenerateOtp}
                                            
                                        >
                                            {isGeneratingOtp ? 'Generating OTP...' : 'Generate OTP'}
                                        </button>
                                    ) : (
                                        <div className="mt-3">
                                            <div className="input-group mb-3">
                                                <input 
                                                    type="text"
                                                    className="form-control"
                                                    placeholder={`Enter ${otpLength}-digit OTP`}
                                                    value={otp}
                                                    onChange={handleOtpChange}
                                                    maxLength={otpLength}
                                                />
                                            </div>
                                            <button 
                                                type="button"
                                                className={`btn ${otp.length !== otpLength ? 'commonbotton_disable' : 'btn-success'} btn-cnf`}
                                                disabled={otp.length !== otpLength || isLoading}
                                                onClick={tifinPopupOpen}
                                              
                                            >
                                                {isLoading ? 'Processing...' : 'Submit'}
                                            </button>
                                        </div>
                                    )}
                                    
                                    <div className="mode-sec">
                                        <h4>Select Transfer Mode</h4>
                                        <div className="selctMode mt-3">
                                            <div className="form-check me-3">
                                                <input
                                                    className="form-check-input form-check-input-2"
                                                    type="radio"
                                                    name="channel"
                                                    id="IMPS"
                                                    onChange={() => handleChangeChanel('IMPS')}
                                                    disabled={formDisabled}
                                                />
                                                <label className="form-check-label mt-1 ms-2" htmlFor="IMPS">
                                                    IMPS
                                                </label>
                                            </div>
                                            <div className="form-check">
                                                <input
                                                    className="form-check-input form-check-input-2"
                                                    type="radio"
                                                    name="channel"
                                                    id="NEFT"
                                                    onChange={() => handleChangeChanel('NEFT')}
                                                    defaultChecked={true}
                                                    disabled={formDisabled}
                                                />
                                                <label className="form-check-label mt-1 ms-2" htmlFor="NEFT">
                                                    NEFT
                                                </label>
                                            </div>
                                        </div>
                                    </div>
                                </form>
                            </div>
                        </div>
                    </div>
                </Modal.Body>
                <Modal.Footer>
                    <div className="money-footer-button">
                        <Button 
                            disabled={isVerified || isLoading} 
                            onClick={VerifiedBank} 
                            className={`${isVerified || isLoading ? 'commonbotton_disable' : ''} mr-3`}
                        >
                            {isLoading ? "Verifying..." : "Verify Account"}
                        </Button>
                        <Button onClick={() => {
                            resetInitialMoney();
                            props.onHide();
                        }}>Cancel</Button>
                    </div>
                </Modal.Footer>
            </Modal>

            <TifinPopup
                show={modalShow}
                onHide={handleTpinModalClose}
                setModalShow={setModalShow}
                sendMoney={sendMoney}
                sendMoneyData={recipientData}
                handleAmount={handleAmount}
                initialMoney={initialMoney}
                setInitialMoney={setInitialMoney}
                loading3={isLoading}
                amountError={amountError}
                maxamount={getMaxAmount()}
                setOtp={setOtp}
                onClose={handleTpinModalClose}
                setFormDisabled={setFormDisabled}
                backdrop="static"
            />
        </>
    );
}

export default UserTransferWithOTP;